body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{font-family:"Source Code Pro",monospace;margin:1%}body{background-color:#d4d4dc}#app{text-align:center;height:100%}.App-logo{pointer-events:none;box-shadow:-1px 0px 6px #8885F3;border-radius:50px}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 5s linear;animation:App-logo-spin infinite 5s linear}}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

header{margin-top:0;display:flex;justify-content:center;background-color:#393f4d;position:fixed;top:0;width:100%;left:-15px;right:0;padding:1%;margin-bottom:1.5%;overflow:hidden;box-shadow:5px 3px 5px #888}#banner{display:contents}h1{font-family:"Orbitron",sans-serif;text-shadow:1px 1px #DB593C;color:#db594d}

footer{background-color:#393f4d;margin:0 !important;height:50px;color:#ffff}

.container{padding-top:10%;margin-bottom:5%}#url{padding:10px;width:27%;box-sizing:border-box;border:none;border-bottom:3px solid red;background-color:#d4d4dc}#url:focus{outline:none !important;border:1px solid red;box-shadow:0 0 10px #719ECE}.method{padding:1.5%;border:none;border-left:3px solid red;background-color:#393f4d;color:#ffff}.method:hover{cursor:pointer;background-color:#888;color:#ffffff;font-weight:bolder}#get_btn:focus{outline:none !important;background-color:#61AFFE;border:3px solid red;box-shadow:0 0 10px #719ECE}#post_btn:focus{outline:none !important;background-color:#63CC90;border:3px solid red;box-shadow:0 0 10px #719ECE}#put_btn:focus{outline:none !important;background-color:#EEA02D;border:3px solid red;box-shadow:0 0 10px #719ECE}#del_btn:focus{outline:none !important;background-color:#E63B3A;border:3px solid red;box-shadow:0 0 10px #719ECE}button{padding:2%;border-radius:7%;border-color:#393f4d;box-shadow:0 0 10px #888;background-color:#dd3a3a;color:#ffffff;font-size:larger}button:hover{cursor:pointer;background-color:#dd3a3ad3}button:focus{border:none;box-shadow:none}.requests{background-color:#393f4d;width:50%;height:200px;margin:auto;color:#ffff}

